import React, { FC } from 'react';
import { Link } from 'react-router-dom';
import { RouteNames } from '../routes';
import logo from '../assets/images/logo.png'

const Header: FC = () => {
    return (
        <div className='header'>
            <div className="container container_header">
                <div className="header__wrapper">
                    <Link className='header__link' to={RouteNames.START}>
                        <img src={logo} alt="" className="header__logo"/>
                    </Link>
                    <div className="header__links">

                        <Link className='header__link' to={RouteNames.TEAMS}>
                            Команды
                        </Link>
                        <Link className='header__link' to={RouteNames.RULES}>
                            Правила
                        </Link>
                        <Link className='header__link' to={RouteNames.TRIP}>
                            Как добраться
                        </Link>
                        <Link className='header__link' to={RouteNames.FOOD}>
                            Еда
                        </Link>
                        <Link className='header__link' to={RouteNames.CONTACTS}>
                            Контакты
                        </Link>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Header;