import React, { FC } from 'react';


const Rules: FC = () => {
    return (
        <>
            <div className="main__headil"/>
            <div className="main main_rules">
                <div className="container">
                    <div className="main__wrapper">
                        <div className="main__title">
                            Правила турнира
                        </div>
                        <div className="main__text">
                            <div className="main__subtitle">
                                Столл - до 8
                            </div>
                            <div className="main__subtitle">
                                Ввод диска по правилам Лорда:
                            </div>
                            <div className="main__descr">
                                Брик объявляется при:<br/> 
                                - пересечении диском линии аута выше 2м.<br/>
                                - пересечении линии зоны ниже 0.5м<br/><br/>
                                При касании диска при пулле начало игры - с точки касания
                            </div>
                            <div className="main__subtitle">
                                На поле: 3 мальчика и 2 девочки
                            </div>
                            <div className="main__subtitle">
                                При отсутствии игрока в команде по уважительным причинам, команда может взять пикапа из другой команды из такой же корзины или ниже, предупреждая при этом капитана противоположной команды.
                            </div>
                            <div className="main__links">
                                <div className="main__descr">
                                    Полный регламент смотреть  
                                    <a target="_blank" href='https://vk.com/hse_ulti_tourney' className="main__descr" rel="noreferrer">
                                    &nbsp; здесь.
                                    </a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
};

export default Rules;