import Contacts from "../pages/Contacts";
import Food from "../pages/Food";
import Main from "../pages/Main";
import Rules from "../pages/Rules";
import Teams from "../pages/Teams";
import Trip from "../pages/Trip";


export interface IRoute {
    path: string;
    component: React.ComponentType;
    exact?: boolean;
}

export enum RouteNames {
    START = '/',
    TEAMS = '/teams',
    RULES = '/rules',
    TRIP = '/trip',
    FOOD = '/food',
    CONTACTS = '/contacts'
}

export const allRoutes: IRoute[] = [
    {
        path: RouteNames.START, exact: true, component: Main
    },    
    {
        path: RouteNames.TEAMS, exact: true, component: Teams
    },
    {
        path: RouteNames.RULES, exact: true, component: Rules
    }, 
    {
        path: RouteNames.TRIP, exact: true, component: Trip
    }, 
    {
        path: RouteNames.FOOD, exact: true, component: Food
    }, 
    {
        path: RouteNames.CONTACTS, exact: true, component: Contacts
    }
]